.portfolio {
  vertical-align: top;
  text-align: center; }

.portfolio-card {
  display: inline-block;
  vertical-align: top;
  max-width: 350px;
  margin: 0.5rem; }

.portfolio-card .card-img-top {
  width: 80%; }

@media screen and (min-width: 650px) {
  .portfolio-card {
    width: 45%; } }

@media screen and (min-width: 900px) {
  .portfolio-card {
    width: 31%;
    max-width: 325px; } }



